import * as React from 'react';
import styled from 'styled-components';

import {Path} from '../../styleType/path';

const Videos = styled.video({
  maxWidth:"1000px",
  marginTop:"20px",
  marginBottom:"20px",
  width: "100%",
  height: "100%",
});

function Video(props:Path)
{
  return(
    <div>
      <Videos 
        controls
        webkit-playsinline
        muted
        loop
        autoPlay
        src={props.url}/>
    </div>
  )
}

export default Video;